.controls {
    display: none
}

.controls__button {
    @include reset;
    position: relative;
    width: 42px;
    height: 42px;

    &::before {
        @include pseudo;
        position: absolute;
        bottom: -9px;
        left: 50%;
        width: 30px;
        height: 30px;
        background: linear-gradient(
            89.23deg,
            #fcaf45 0.66%,
            #f77737 20.96%,
            #fd1d1d 41.27%,
            #c13584 61.57%,
            #833ab4 82.41%,
            #405de6 103.25%
        );
        border-radius: 50%;
        transform: translateX(-50%);
        filter: blur(18px);
        pointer-events: none;
    }

    &--prev {
        .controls__inner {
            transform: rotate(-180deg);
        }
    }

    &:hover {
        &::after {
            @include textGradient;
        }

        .controls__inner {
            background: url("../../img/arrow@hover.svg"), #ffffff;
            background-repeat: no-repeat;
            background-position: center;
            background-size: 13px;
        }
    }
}

.controls__inner {
    @include pseudo;
    position: relative;
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 42px;
    height: 42px;
    background: url("../../img/arrow.svg"), $g-main;
    background-repeat: no-repeat;
    background-position: center;
    //background-size: 13px;
    border-radius: 50%;
}

@media (min-width: 1280px) {
    .controls__button:not(:last-child) {
        margin-right: 267px;
    }

    }
